import React from 'react';
import Slider from "react-slick";

class Carousel extends React.Component {
    render() {
      var settings = {
        dots: true,
        infinite: true,
        slidesToShow: 2,
        slidesToScroll: 1,
            infinite: true,
            autoplay: true,
            autoplaySpeed: 5000,
            pauseOnHover: false,
            centerMode: true,
            centerPadding: "0"
      
      }
      
      return (
        <div className="container sizing">
          <Slider {...settings}>
            <div>
              <img src="https://static.vibe.com/files/2018/01/black-panther-poster-1517325222-640x933.jpg" />
            </div>
            <div>
            <img src="https://i.ebayimg.com/images/g/VckAAOSwEK9UCOef/s-l1600.jpg" />
            </div>
            <div>
            <img src="https://i.ebayimg.com/images/g/Tf8AAOSwnGJWS4uw/s-l1600.jpg" />
            </div>
            <div>
            <img src="http://img.auctiva.com/imgdata/1/1/7/9/1/0/1/webimg/560106212_o.jpg" />
            </div>
            <div>
            <img src="https://i.ebayimg.com/images/g/CV4AAOSwGvhUCOid/s-l1600.jpg" />
            </div>
            <div>
            <img src="https://i.ebayimg.com/images/g/mnIAAOSwnipWWiwy/s-l1600.jpg" />
            </div>
              <div>
            <img src="https://jbchost.com.br/akibaspace/media/2017/02/promologan.jpg" />
            </div>
            <div>
            <img src="http://digitalspyuk.cdnds.net/17/12/480x681/john-wick-chapter-2-poster.jpg" />
            </div>
            <div>
            <img src="https://www.dvdsreleasedates.com/posters/800/T/The-Avengers-2012-movie-poster.jpg" />
            </div>
            <div>
            <img src="  http://media.comicbook.com/2017/10/guardians-of-the-galaxy-movie-poster-marvel-cinematic-universe-1038897.jpg" />
            </div>
          
          </Slider>
          </div>
      );
    }
  }
  
  export default Carousel;


